import './PiedPage.scss';

export default function PiedPage() {
    return (
        // Les {} dfinissent une zone de JS, et la fonction a l'intrieur retourne l'anne courante
        // &copy devient le symbole de copyright
        <footer className='PiedPage'>
            &copy;{new Date().getFullYear()} - TIM Maisonneuve - Tous droits rservs
        </footer>
    );
}